import React, { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import "../styles/Page3.css";

interface LocationState {
  selectedEquipment?: string;
}

interface BattleState {
  playerHits: number;
  monsterHits: number;
  isPlayerTurn: boolean;
  isGameOver: boolean;
  playerWon: boolean;
  storyShown: boolean;
}

const Page3: React.FC = () => {
  const location = useLocation();
  const state = location.state as LocationState;
  const equipment = state?.selectedEquipment || " ";

  const [isRolling, setIsRolling] = useState(false);
  const [diceValue, setDiceValue] = useState<number | null>(null);
  const [isTurnTransitioning, setIsTurnTransitioning] = useState(false);
  const [battleState, setBattleState] = useState<BattleState>({
    playerHits: 0,
    monsterHits: 0,
    isPlayerTurn: true,
    isGameOver: false,
    playerWon: false,
    storyShown: false,
  });
  const [battleLog, setBattleLog] = useState<string[]>([]);
  const [isAnimating, setIsAnimating] = useState(false);
  const [gameOver, setGameOver] = useState(false);

  const getDiceRange = (equipment: string): [number, number] => {
    switch (equipment) {
      case " ":
        return [3, 6];
      case " ":
        return [2, 6];
      case "  ":
        return [1, 4];
      default: //  
        return [1, 6];
    }
  };

  const rollDice = (): number => {
    const [min, max] = getDiceRange(equipment);
    const value = Math.floor(Math.random() * (max - min + 1)) + min;
    setDiceValue(value);
    setIsRolling(true);

    setTimeout(() => {
      setIsRolling(false);
    }, 2000);

    return value;
  };

  const handlePlayerTurn = () => {
    const [min, max] = getDiceRange(equipment);
    const diceValue = Math.floor(Math.random() * (max - min + 1)) + min;
    setDiceValue(diceValue);
    setIsRolling(true);

    setTimeout(() => {
      setIsRolling(false);
      setBattleLog((prev) => [...prev, `: ${diceValue}`]);

      if (diceValue === 6) {
        setBattleLog((prev) => [...prev, "!  !"]);
        setBattleState((prev) => ({
          ...prev,
          monsterHits: 2,
          isGameOver: true,
          playerWon: true,
        }));
      } else if (diceValue >= 3) {
        setBattleLog((prev) => [...prev, " !"]);
        setBattleState((prev) => {
          const newHits = prev.monsterHits + 1;
          return {
            ...prev,
            monsterHits: newHits,
            isGameOver: newHits >= 2,
            playerWon: newHits >= 2,
          };
        });
        if (battleState.monsterHits + 1 >= 2) {
          setBattleLog((prev) => [...prev, " !"]);
        }
      } else {
        setBattleLog((prev) => [...prev, " ..."]);
      }

      setIsTurnTransitioning(true);
      setTimeout(() => {
        setBattleState((prev) => ({
          ...prev,
          isPlayerTurn: false,
        }));
        setIsTurnTransitioning(false);
      }, 1000);
    }, 2000);
  };

  const handleMonsterTurn = () => {
    const diceValue = Math.floor(Math.random() * 4) + 1;
    setDiceValue(diceValue);
    setIsRolling(true);

    setTimeout(() => {
      setIsRolling(false);
      setBattleLog((prev) => [...prev, ` : ${diceValue}`]);

      if (diceValue === 3 || diceValue === 4) {
        setBattleLog((prev) => [...prev, "  !"]);
        setBattleState((prev) => {
          const newHits = prev.playerHits + 1;
          return {
            ...prev,
            playerHits: newHits,
            isGameOver: newHits >= 2,
            playerWon: false,
          };
        });
        if (battleState.playerHits + 1 >= 2) {
          setBattleLog((prev) => [...prev, " ..."]);
        }
      } else {
        setBattleLog((prev) => [...prev, "  !"]);
      }

      setIsTurnTransitioning(true);
      setTimeout(() => {
        setBattleState((prev) => ({
          ...prev,
          isPlayerTurn: true,
        }));
        setIsTurnTransitioning(false);
      }, 1000);
    }, 2000);
  };

  const handleRollClick = () => {
    if (isRolling || battleState.isGameOver || isTurnTransitioning) return;

    if (battleState.isPlayerTurn) {
      setBattleLog((prev) => [...prev, " !"]);
      handlePlayerTurn();
    } else {
      setBattleLog((prev) => [...prev, " !"]);
      handleMonsterTurn();
    }
  };

  const startBattle = () => {
    setBattleState((prev) => ({
      ...prev,
      storyShown: true,
    }));
  };

  return (
    <div className="page3-container">
      <div className="battle-box">
        <h2> </h2>
        <div className="equipment-info"> : {equipment}</div>

        {!battleState.storyShown ? (
          <div className="story-section">
            <div className="story-text">
              <p>      </p>
              <p>   !</p>
            </div>
            <button className="start-battle-button" onClick={startBattle}>
               
            </button>
          </div>
        ) : (
          !battleState.isGameOver && (
            <>
              <div className="battle-status">
                <div className="health-status">
                  <span className="status-label"></span>
                  <div className="hearts">
                    {[...Array(2)].map((_, index) => (
                      <span
                        key={index}
                        className={`heart ${
                          index < 2 - battleState.playerHits ? "full" : "empty"
                        }`}
                      >
                        
                      </span>
                    ))}
                  </div>
                </div>
                <div className="health-status">
                  <span className="status-label"></span>
                  <div className="hearts">
                    {[...Array(2)].map((_, index) => (
                      <span
                        key={index}
                        className={`heart ${
                          index < 2 - battleState.monsterHits ? "full" : "empty"
                        }`}
                      >
                        
                      </span>
                    ))}
                  </div>
                </div>
              </div>

              <div className="turn-info">
                {battleState.isPlayerTurn
                  ? " !"
                  : " !"}
              </div>

              {diceValue && (
                <div className="dice-result">
                  <div className="dice-title">
                    {battleState.isPlayerTurn ? "" : ""} 
                  </div>
                  <div className={`dice-value ${isRolling ? "rolling" : ""}`}>
                    {isRolling ? "?" : diceValue}
                  </div>
                </div>
              )}

              <button
                className="roll-button"
                onClick={handleRollClick}
                disabled={
                  isRolling || battleState.isGameOver || isTurnTransitioning
                }
              >
                 
              </button>
            </>
          )
        )}

        <div className="battle-log">
          {battleLog.map((log, index) => (
            <div key={index} className="log-entry">
              {log}
            </div>
          ))}
        </div>

        {battleState.isGameOver && (
          <div className="game-over">
            <h3>{battleState.playerWon ? "!" : "Game Over"}</h3>
            {battleState.playerWon ? (
              <Link to="/page4" className="next-button" state={{ equipment }}>
                
              </Link>
            ) : (
              <Link to="/" className="home-button">
                
              </Link>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default Page3;
