import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "../styles/Page2.css";

type Equipment =
  | " "
  | " "
  | " "
  | "  ";

interface DiceResult {
  value: number;
}

const Page2: React.FC = () => {
  const navigate = useNavigate();
  const [diceResult, setDiceResult] = useState<DiceResult | null>(null);
  const [isRolling, setIsRolling] = useState(false);
  const [selectedEquipment, setSelectedEquipment] = useState<Equipment | null>(
    null
  );

  const equipments: Equipment[] = [
    " ",
    " ",
    " ",
    "  ",
  ];

  const rollDice = () => {
    setIsRolling(true);
    setSelectedEquipment(null);

    //   
    const rollInterval = setInterval(() => {
      setDiceResult({
        value: Math.floor(Math.random() * 6) + 1,
      });
    }, 100);

    // 2    
    setTimeout(() => {
      clearInterval(rollInterval);
      const finalValue = Math.floor(Math.random() * 6) + 1;
      setDiceResult({ value: finalValue });
      setIsRolling(false);
    }, 2000);
  };

  const isEquipmentSelectable = (equipment: Equipment): boolean => {
    if (!diceResult) return false;

    const value = diceResult.value;

    switch (equipment) {
      case " ":
        return value === 6;
      case " ":
        return value >= 5;
      case " ":
        return value >= 2;
      case "  ":
        return true; //   
      default:
        return false;
    }
  };

  const handleEquipmentSelect = (equipment: Equipment) => {
    setSelectedEquipment(equipment);
  };

  return (
    <div className="page2-container">
      <div className="equipment-selection-box">
        <h2> </h2>

        {diceResult && (
          <div className="dice-result">
            <div className="dice-title"> </div>
            <div className={`dice-value ${isRolling ? "rolling" : ""}`}>
              {isRolling ? "?" : diceResult.value}
            </div>
          </div>
        )}

        {!diceResult && (
          <button
            className="roll-button"
            onClick={rollDice}
            disabled={isRolling}
          >
             
          </button>
        )}

        {diceResult && !isRolling && !selectedEquipment && (
          <div className="equipment-list">
            {equipments.map((equipment) => (
              <button
                key={equipment}
                className={`equipment-button ${
                  !isEquipmentSelectable(equipment) ? "disabled" : ""
                }`}
                onClick={() => handleEquipmentSelect(equipment)}
                disabled={!isEquipmentSelectable(equipment)}
              >
                {equipment}
                {equipment === "  " && (
                  <span className="always-available">(  )</span>
                )}
              </button>
            ))}
          </div>
        )}

        {selectedEquipment && (
          <div className="selection-result">
            <p> : {selectedEquipment}</p>
            <Link
              to="/page3"
              className="next-button"
              state={{ selectedEquipment }}
            >
              
            </Link>
          </div>
        )}

        <div className="equipment-rules">
          <h3> </h3>
          <ul>
            <li>6:    </li>
            <li>5:     </li>
            <li>2~4:     </li>
            <li>1:     </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Page2;
